.helpIcon {
  width: 24px;
  height: 24px;
}

.textInt {
  box-shadow: 0px -7px 14px -1px rgba(239,131,48, .75);

  display: none;
  z-index: 10000;
  width: 300px;
  height: auto;
  margin-right: 5px;
  margin-left: 5px;
  margin-top: 25px;
  position: absolute;
  top: 110%;
  right: 0;
  background: #f98330;
  text-align: justify;
  color: white;
  padding: 1.5rem;
  opacity: 0.95;
  border-radius: 15px;
}

.helper:hover .textInt {
  display: block;
}

.link:hover + .text {
  display: block;
}

li {
  list-style: none;
}

li:before {
  content: "\2192";
}

.endLine {
  text-align: center;
  font-family: "Rockwell";
}
